import React, { useContext } from 'react';
import animationContext from '../../context/animationContext';
import { Redirect, useLocation } from 'react-router-dom';
import { Animated } from 'react-animated-css';

import './About.css';
import controle from '../../images/controle.png';
import bicicleta from '../../images/bicicleta.png';
import pc from '../../images/pc.png';
import Navigate from '../../components/Navigate';

export default function About() {
  const { location, visibleContent } = useContext(animationContext);

  const { pathname } = useLocation();
  const isVisible = visibleContent === '/about';

  if (pathname !== location) return <Redirect to={location} />;

  return (
    <Animated
      animationIn="fadeIn"
      animationInDuration={1000}
      animationOutDuration={300}
      animationOut="fadeOut"
      isVisible={isVisible}
    >
      <div className="page">
        <h2 className="title">Sobre mim</h2>
        <Navigate left="/" right="/skills" />
        <div className="about">
          <article className="left">
            <p>
              Ol, meu nome  Higor Maranho Nunes e tenho 22 anos, atualmente
              moro em Borborema, Paraba. Desde pequeno sempre fui muito ativo,
              gostava de jogar futebol na rua, andar de bicicleta e me divertir
              com outras atividades tpicas da minha idade. Aos 17 anos,
              descobri minha paixo pela programao e comecei a passar horas em
              frente ao computador estudando e aprendendo sobre a rea.
              <br />
              <br />
              Aos 19 anos, decidi me inscrever na faculdade de Analise e
              Desenvolvimento de Sistemas, mas infelizmente tive que interromper
              meus estudos por questes financeiras. Mas no desisti, continuei
              me atualizando e aprendendo cada vez mais sobre programao e
              tecnologia. Foi ento que em 2022, conheci a Trybe, uma escola de
              desenvolvimento de software, e decidi me inscrever. Aps uma
              segunda tentativa, consegui ser selecionado e atualmente fao
              parte da turma 24.
              <br />
              <br />
              Durante minha jornada na rea de tecnologia, tambm prestei
              servios de manuteno de computadores por mais de 5 anos, o que
              me permitiu conhecer diferentes pessoas e culturas, aprimorando
              minha comunicao e tratamento com as pessoas. Acredito que todas
              essas experincias foram fundamentais para minha formao pessoal
              e profissional, pois me permitiram adquirir habilidades
              importantes e crescer como pessoa e profissional.
              <br />
              <br />
              Alm disso, meus hobbies e gostos continuam sendo muito
              importantes para mim, pois me mantm inspirado e motivado a sempre
              buscar o melhor em mim e nos projetos em que estou envolvido.
            </p>
          </article>
          <div className="right">
            <div className="images-container">
              <img src={controle} alt="" />
              <img src={pc} alt="" />
              <Animated
                className="image-animated"
                animationIn="bounceInLeft"
                animationInDelay={600}
                animationInDuration={1000}
                animationOutDuration={300}
                animationOut="fadeOut"
                isVisible={isVisible}
              >
                <img className="image-bicicleta" src={bicicleta} alt="" />
              </Animated>
            </div>
          </div>
        </div>
      </div>
    </Animated>
  );
}
